/* ----------------------------------- */
body {
  font-family: "Aileron", sans-serif;
  font-display: swap;
  background-color: #141414;
}

.header {
  background: url("../components/img/phoneOne.png");
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100vh;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.header__container {
  display: grid;
  height: 100%;
  grid-template-rows: 1fr auto;
  max-width: 1400px;
  margin: 0 auto;
  padding: 15px;
}
.header__logo {
  transition: 0.3s;
}
@media (max-width: 840px) {
  .header__logo {
    grid-column: 3/4;
    text-align: right;
  }
}
.header__logo-svg {
  width: 120px;
  height: 100px;
}
@media (max-width: 840px) {
  .header .header-block {
    grid-column: 1;
    grid-row: 1;
  }
}
.header .header-block__container {
  grid-row: 1/3;
  grid-column: 2/3;
  background-color: hsla(0, 0%, 8%, 0.7);
  max-width: 600px;
  justify-self: right;
  min-width: 33vw;
}
@media (max-width: 840px) {
  .header .header-block__container {
    min-width: 500px;
    grid-row: 2/4;
    justify-self: center;
    grid-column: 1/4;
  }
}
@media (max-width: 550px) {
  .header .header-block__container {
    min-width: auto;
    width: 100%;
    max-width: none;
  }
}
.header .header-block__title {
  font-size: 73px;
  max-width: 300px;
  margin-bottom: 30px;
  display: flex;
  flex-direction: column;
}
@media (max-width: 840px) {
  .header .header-block__title {
    padding-top: 10px;
    font-size: 24px;
    flex-direction: row;
    gap: 10px;
  }
}
@media (max-width: 550px) {
  .header .header-block__title {
    font-size: 20px;
  }
}
.header .header-block__text {
  font-size: 20px;
  color: #9b9b9b;
  margin-bottom: 40px;
  font-family: "Aileron";
}
.header .header-register {
  padding: 30px;
  height: 100%;
  display: flex;
  flex-direction: column;
  gap: 30px;
}
@media (max-width: 550px) {
  .header .header-register {
    padding: 20px;
  }
}
.header .header-register__block {
  flex: 1 1 auto;
}
.header .header-register__title {
  text-align: center;
  font-size: 42px;
  margin-bottom: 30px;
}
.header .register-block {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 25px;
}
.header .register-block__field {
  background-color: #fff;
  width: 100%;
}
@media (max-width: 479px) {
  .header .register-block__field {
    width: 100%;
  }
}
.header .register-block__button {
  font-size: 15px;
}
.header .footer-social {
  display: flex;
  justify-content: center;
  align-items: end;
  gap: 30px;
}
@media (max-width: 479px) {
  .header .footer-social {
    flex-wrap: wrap;
    justify-content: start;
  }
}
.header .footer-social a h2 {
  margin-bottom: 15px;
}
.header .footer-social__gmail-title {
  position: relative;
  padding-left: 30px;
}
.header .footer-social__gmail-title::after {
  content: "";
  margin-top: -7px;
  width: 25px;
  height: 25px;
  position: absolute;
  top: 0;
  left: 0;
  background: url("../components/img/gmail_new_logo_icon_159149.svg") 25px 25px/cover;
}
.header .footer-social__telegram-title {
  position: relative;
  padding-left: 30px;
}
.header .footer-social__telegram-title::after {
  content: "";
  margin-top: -7px;
  width: 25px;
  height: 25px;
  position: absolute;
  top: 0;
  left: 0;
  background: url("../components/img/telegram_logo_icon_168692.svg") 25px 25px/cover;
}

/* ----------------------------------- */
@media (max-width: 390px) {
  .main {
    padding-top: 50px;
  }
}

.service {
  padding-top: 50px;
}
.service__container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 15px;
  display: grid;
  grid-template-columns: 50% 50%;
  gap: 15px;
}
@media (max-width: 840px) {
  .service__container {
    grid-template-columns: 1fr;
  }
}
.service .service-block__title {
  font-size: 42px;
  margin-bottom: 60px;
}
@media (max-width: 840px) {
  .service .service-block__img {
    display: none;
  }
}
.service .service-block__img img {
  width: 100%;
  max-width: 620px;
  max-height: 300px;
}
.service .service-content {
  padding-top: 10px;
}
.service .service-content__block {
  display: grid;
  grid-template-columns: 40px 1fr;
  gap: 10px;
}
.service .service-content__number {
  font-size: 30px;
  color: #9b9b9b;
}
.service .service-content__title {
  font-size: 26px;
}
.service .service-content__text {
  grid-column: 2/3;
  font-size: 14px;
  margin-bottom: 20px;
}

.about {
  font-size: 15px;
}
@media (max-width: 840px) {
  .about__container {
    text-align: center;
    display: block;
    max-width: none;
  }
}
.about .about-block {
  max-width: 450px;
}
@media (max-width: 840px) {
  .about .about-block {
    max-width: none;
  }
}
.about .about-block__title {
  margin-bottom: 35px;
}
.about .about-block__text {
  margin-bottom: 35px;
}
.about .about-content__img {
  text-align: center;
}
.about .about-content__img img {
  max-width: 620px;
  max-height: 420px;
  width: 100%;
}
