$font-name: "Aileron";

@font-face {
  font-family: $font-name;
  src: url('../components/fonts/Aileron-Regular.otf') format('opentype');
  font-weight: normal;
  font-style: normal;
}
*{
	padding: 0%;
	margin: 0;
	border: 0;

}
*, *:before, *:after{
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
}
:focus, :active{outline: none;}
a:focus, a:active{outline: none;}
nav,footer,header,aside{display: block;}
html,body{
    overflow-x: hidden;
	height: 100%;
	width: 100%;
	font-size: 100%;
	line-height: 1;
	font-size: 14px;
	-ms-text-size-adjust: 100%;
	-moz-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
    color: white;


}
input,button,textarea{
	font-family: inherit;
}
input::-ms-clear{display: none;}
button{cursor: pointer;}
button::-moz-focus-inner {padding: 0; border: 0;}
a, a:visited{text-decoration: none;}
ul li{list-style: none;}
img{vertical-align: top;}

h1,h2,h3,h4,h5,h6{font-size: inherit; font-weight: 400;}

a{color: white;}
/* ----------------------------------- */
/* ----------------------------------- */
// Import regular font

body {
    font-family: $font-name, sans-serif;
    font-display: swap; // Add any additional parameters here
    background-color: #141414;
  }
  
.header{
    // height: 100vw;
    background:  url("../components/img/phoneOne.png") ;
    background-repeat: no-repeat;
    background-size:cover;
    width: 100%;
    height: 100vh;
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    &__container{
        display: grid;
        height: 100%;
        grid-template-rows: 1fr auto;
        max-width: 1400px;
        margin: 0 auto;
        padding: 15px;
        @media (max-width:840px){
            // grid-template-rows: 1fr 1fr 1fr;
        }
    }
    &__logo{
        transition:0.3s;
        @media (max-width:840px){
            grid-column: 3/4;
            text-align: right;
        }
    }
    &__logo-svg{
            width: 120px;
            height: 100px;
    }
    .header-block{
        @media (max-width:840px){
            grid-column: 1;
            grid-row: 1;
        }
        &__container{
            grid-row: 1/3;
             grid-column: 2/3; 
             background-color: hsl(0, 0%, 8%,0.7);
              max-width: 600px;
              justify-self: right;
              min-width: 33vw;
              @media (max-width:840px){
                  min-width: 500px;
                grid-row: 2/4;
                justify-self: center;
                grid-column: 1/4;
            }
            @media (max-width:550px){
                min-width: auto;
                width: 100%;
                max-width: none;
            }
        }
        &__title{
            font-size: 73px;
            max-width: 300px;
            margin-bottom: 30px;
            display: flex;
            flex-direction: column;
            @media (max-width:840px){
                padding-top: 10px;
                font-size: 24px;
                flex-direction: row;
                gap: 10px;
            }
            @media (max-width:550px){
                font-size: 20px;
            }
        }
        &__text{
            font-size: 20px;
            color: #9b9b9b;
            margin-bottom: 40px;
            font-family: $font-name;
        }
    }
    
    .header-register{
        padding: 30px;
        height: 100%;
        display: flex;
        flex-direction: column;
        gap: 30px;
        @media (max-width:550px){
            padding:20px;
        }
        &__register-block{
            
        }
        &__block{
            flex: 1 1  auto;
            
        }
        &__title{
            text-align: center;
            font-size: 42px;
            margin-bottom: 30px;
        }
    }
    .register-block{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        gap: 25px;
        &__field{
            background-color: #fff;
            width: 100%;
            @media (max-width:479px) {
                width:100%;
            }
        }
        &__button{
            font-size: 15px;
        }
    }
    .footer-social{
        display: flex;
        justify-content: center;
        align-items: end;
        gap: 30px;
        @media (max-width:479px) {
            flex-wrap: wrap;
            justify-content: start;
        }
        & a{
            & h2 {
                margin-bottom: 15px;
            }
        }
        &__gmail-title{
            position: relative;
            padding-left: 30px;
            &::after{
                content: '';
                margin-top: -7px;
                width: 25px;
                height: 25px;
                position: absolute;
                top: 0;
                left: 0;
                background: url("../components/img/gmail_new_logo_icon_159149.svg") 25px 25px/ cover ;
            }
        }
        &__telegram-title{
            position: relative;
            padding-left: 30px;
            &::after{
                content: '';
                margin-top: -7px;
                width: 25px;
                height: 25px;
                position: absolute;
                top: 0;
                left: 0;
                background: url("../components/img/telegram_logo_icon_168692.svg") 25px 25px/ cover ;
            }
        }
    }
}
/* ----------------------------------- */
.main{
    @media (max-width:390px) {
        padding-top: 50px;
    }

}
.service{
    padding-top: 50px;
    &__container{
        max-width: 1400px;
        margin: 0 auto;
        padding: 15px;
        display: grid;
        grid-template-columns: 50% 50%;
        gap: 15px;
        @media (max-width:840px) {
            grid-template-columns: 1fr;
        }
    }
    .service-block{
        &__title{
            font-size: 42px;
            margin-bottom: 60px;
        }
        &__img{
            @media (max-width:840px) {
                display: none;
            }
            & img{
                width:100%;
                max-width: 620px;
                max-height: 300px;
            }
        }
    }
    .service-content{
        padding-top: 10px;
        &__block{
            display: grid;
            grid-template-columns: 40px 1fr;
            gap: 10px;
        }
        &__number{
            font-size: 30px;
            color: #9b9b9b;
        }
        &__title{
            font-size: 26px;
        }
        &__text{
            grid-column: 2/3;
            font-size: 14px;
            margin-bottom: 20px;
            
        }
    }
}
.about{
    font-size: 15px;
    &__container{
        @media (max-width:840px) {
            text-align: center;
            display: block;
            max-width: none;
        }
    }
    .about-block{
        max-width: 450px;
        @media (max-width:840px) {
            max-width: none;
        }
        &__title{
            margin-bottom: 35px;
        }
        &__text{
            margin-bottom: 35px;
            
        }
    }
    .about-content{
        &__img{
            text-align: center;
            & img{
                max-width: 620px;
                max-height: 420px;
                width: 100%;
                @media (max-width:840px) {
                }
            }
        }
    }
}

/* ----------------------------------- */

.footer{
    position: relative;
    padding-top: 70px;
    padding-bottom: 70px;
    &__container{
        max-width: 1400px;   
        padding: 15px;
        margin: 0 auto;
    }
    .footer-register{
        display: grid;
        grid-template-columns: 1fr 1fr;
        @media (max-width:840px) {
            display: block;
        }
        &__title{
            font-size: 42px;
            text-align: center;
        }
        &__img{
            @media (max-width:840px) {
                display: none;
            }
            & img {
                max-width: 580px;
                max-height: 620px;
                width: 100%;
            }
        }
    }
    .register-block{
        padding-top: 50px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 30px;
        &__field{
            background-color: #fff;
            width: 300px;
            @media (max-width:479px) {
                width:100%;
            }
        }
        &__button{
            width: 300px;
            font-size: 15px;
            @media (max-width:479px) {
                width:100%;
            }
        }
    }
    .footer-social{
        padding-top: 150px;
        display: flex;
        justify-content: center;
        gap: 50px;
        @media (max-width:840px) {
            padding-top: 100px;
        }
        @media (max-width:479px) {
            flex-wrap: wrap;
            justify-content: start;
        }
        & a{
            & h2 {
                margin-bottom: 15px;
            }
        }
        &__gmail-title{
            position: relative;
            padding-left: 30px;
            &::after{
                content: '';
                margin-top: -7px;
                width: 25px;
                height: 25px;
                position: absolute;
                top: 0;
                left: 0;
                background: url("../components/img/gmail_new_logo_icon_159149.svg") 25px 25px/ cover ;
            }
        }
        &__telegram-title{
            position: relative;
            padding-left: 30px;
            &::after{
                content: '';
                margin-top: -7px;
                width: 25px;
                height: 25px;
                position: absolute;
                top: 0;
                left: 0;
                background: url("../components/img/telegram_logo_icon_168692.svg") 25px 25px/ cover ;
            }
        }
    }
    .copyright{
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        background: #fff;
        color: #141414;
        text-align: center;
        padding: 15px;
    }
}
.accept{
    pointer-events:none;
    opacity: 0.5;
}